import React, {Component, Fragment} from 'react';
import {animateScroll as scroll} from "react-scroll";
import {CSSTransition} from "react-transition-group";

export default class Footer extends Component {

    state = {
        showGoTop: false
    };

    showGoTop = () => {
        this.setState({
            showGoTop: true
        });
    };

    hideGoTop = () => {
        this.setState({
            showGoTop: false
        });
    };

    scrollToTop = (e) => {
        e.preventDefault();
        scroll.scrollToTop();
    };

    render() {
        const {showGoTop} = this.state;
        const {socialLinks} = this.props.resumeData;

        return (
            <Fragment>
                <footer id="footer">
                    <div className="row">
                        <div className="col-six tab-full pull-right social">
                            <ul className="footer-social">
                                {
                                    socialLinks.map(item => (
                                        <li key={item.link}><a href={item.link}><i className={item.icon}/></a></li>
                                    ))
                                }
                            </ul>
                        </div>
                        <div className="col-six tab-full">
                            <div className="copyright">
                                <span>Â© Copyright {(new Date()).getFullYear()}.</span>
                            </div>
                        </div>
                        <CSSTransition
                            in={showGoTop}
                            timeout={300}
                            classNames="back-to-top"
                            unmountOnExit
                        >
                            <div id="go-top">
                                <a className="smoothscroll" title="Back to Top" href="#" onClick={this.scrollToTop}>
                                    <i className="fa fa-chevron-up"/>
                                </a>
                            </div>
                        </CSSTransition>
                    </div>
                    {/* /row */}
                </footer>
            </Fragment>
        );
    }
}