import React, {Component, Fragment} from 'react';
import {Link, animateScroll as scroll} from "react-scroll";
import {CSSTransition} from 'react-transition-group';

export default class Header extends Component {

    state = {
        hamburgerShown: false,
        currentItem: 'intro',
        menuShown: false
    };

    onClickMenu = (e) => {
        e.preventDefault();
        this.setState({
            hamburgerShown: !this.state.hamburgerShown,
            menuShown: !this.state.menuShown
        });
    };

    scrollToTop = () => {
        this.setState({
            currentItem: 'intro'
        });
        scroll.scrollToTop();
    };

    onClickNavItem = (e, link) => {
        e.preventDefault();
        this.setState({
            hamburgerShown: !this.state.hamburgerShown,
            currentItem: link,
            menuShown: !this.state.menuShown
        });
    };

    onSectionReached = (section) => {
        this.setState({
            currentItem: section
        });
    };

    getNavItemClass = (item) => {
        return this.state.currentItem === item ? 'current' : undefined;
    };

    render() {
        const menuClass = this.state.hamburgerShown ? 'menu-toggle is-clicked' : 'menu-toggle';
        const navItemClass = this.state.hamburgerShown ? 'smoothscroll is-clicked' : 'smoothscroll';
        const {menuShown} = this.state;

        const {navItems} = this.props.resumeData;

        return (
            <Fragment>
                <header id="home">
                    <div className="row">
                        <div className="top-bar">
                            <a className={menuClass} href="#"
                               onClick={(e) => this.onClickMenu(e)}><span>Menu</span></a>
                            <div className="logo">
                                <a href="index.html">PORTFOLIO</a>
                            </div>
                            <nav id="main-nav-wrap">
                                <CSSTransition
                                    in={menuShown}
                                    timeout={300}
                                    classNames="nav"
                                    unmountOnExit
                                >
                                    <ul className="main-navigation">
                                        {
                                            navItems.map(item => (
                                                <li className={this.getNavItemClass(item.id)} key={item.id}>
                                                    <Link
                                                        className={navItemClass}
                                                        href="#"
                                                        onClick={(e) => this.onClickNavItem(e, item.id)}
                                                        to={item.id}
                                                        spy={true}
                                                        smooth={true}
                                                        offset={0}
                                                        duration={500}
                                                    >
                                                        {item.title}
                                                    </Link>
                                                </li>
                                            ))
                                        }
                                    </ul>
                                </CSSTransition>
                            </nav>
                        </div>
                        {/* /top-bar */}
                    </div>
                    {/* /row */}
                </header>
            </Fragment>
        );
    }
}