import React, {Component, Fragment} from 'react';
import {Link} from "react-scroll";
import Waypoint from 'react-waypoint';

export default class About extends Component {

    onEnter = () => {
        this.props.onSectionReached('about');
        this.props.showGoTop();
    };

    render() {
        let data = this.props.resumeData;

        return (
            <Fragment>
                <Waypoint onEnter={this.onEnter} bottomOffset="50%">
                    <div>
                        <section id="about">
                            <div className="row section-intro">
                                <div className="col-twelve">
                                    <h5>About</h5>
                                    <h1>Let me introduce myself.</h1>
                                    <div className="intro-info">
                                        <img src={data.profilePic} alt="Profile"/>
                                        <p className="lead">{data.presentation}</p>
                                    </div>
                                </div>
                            </div>
                            {/* /section-intro */}
                            <div className="row about-content">
                                <div className="col-six tab-full">
                                    <h3>Profile</h3>
                                    <p>{data.profile}</p>
                                    <ul className="info-list">
                                        <li>
                                            <strong>Fullname:</strong>
                                            <span>{data.fullname}</span>
                                        </li>
                                        <li>
                                            <strong>Birth Date:</strong>
                                            <span>{data.birthdate}</span>
                                        </li>
                                        <li>
                                            <strong>Job:</strong>
                                            <span>{data.job}</span>
                                        </li>
                                        <li>
                                            <strong>Website:</strong>
                                            <span>{data.website}</span>
                                        </li>
                                        <li>
                                            <strong>Email:</strong>
                                            <span>{data.email}</span>
                                        </li>
                                    </ul>
                                    {/* /info-list */}
                                </div>
                                <div className="col-six tab-full">
                                    <h3>Skills</h3>
                                    <p>{data.skillsDesc}</p>
                                    <ul className="skill-bars">
                                        {
                                            data.skills.map(item => {
                                                return (
                                                    <li key={item.skill}>
                                                        <div className={"progress percent" + item.percent}>
                                                            <span>{item.percent}%</span></div>
                                                        <strong>{item.skill}</strong>
                                                    </li>
                                                )
                                            })
                                        }
                                    </ul>
                                </div>
                            </div>
                            <div className="row button-section">
                                <div className="col-twelve">
                                    <Link
                                        title="Hire Me"
                                        className="button stroke smoothscroll"
                                        to="contact"
                                        spy={true}
                                        smooth={true}
                                        offset={0}
                                        duration={500}
                                    >
                                        Hire Me
                                    </Link>
                                    <a title="Download CV" href="https://drive.google.com/open?id=1niuNDEpNv4pEfPiGhQZZpef1OJF_4ci8" className="button button-primary">Download CV</a>
                                </div>
                            </div>
                        </section>
                    </div>
                </Waypoint>
            </Fragment>
        );
    }
}