import React, {Component, Fragment} from 'react';
import axios from 'axios';

export default class Loader extends Component {

    state = {
        showingLoader: true
    };

    componentDidMount() {
        axios.get("https://anelfdz-portfolio.herokuapp.com/api/resumedata")
            .then(response => {
                this.setState({
                    showingLoader: false
                });

                this.props.onResumeDataLoaded(response.data);
            })
            .catch(error => console.error(error));
    }

    render() {
        const {showingLoader} = this.state;
        return (
            <Fragment>
                <div id="preloader" className={showingLoader ? undefined : 'loader-hidden'}>
                    <div id="loader" className={showingLoader ? undefined : 'loader-hidden'}/>
                </div>
            </Fragment>
        );
    }
}