import React, {Component} from 'react';
import Loader from "./components/loader/loader";
import Header from './components/header/header';
import Intro from './components/intro/intro';
import About from './components/about/about';
import Resume from './components/resume/resume';
// import Portfolio from './components/portfolio/portfolio';
// import Services from './components/services/services';
import Stats from './components/stats/stats';
import ContactUs from './components/contactus/contactus';
import Footer from './components/footer/footer';

class App extends Component {

    state = {
        onSectionReached: undefined,
        showGoTop: undefined,
        hideGoTop: undefined,
        resumeData: undefined
    };

    componentDidUpdate() {
        if (!this.state.onSectionReached && this.state.resumeData) {
            this.setState({
                onSectionReached: this.header.onSectionReached,
                showGoTop: this.footer.showGoTop,
                hideGoTop: this.footer.hideGoTop
            });
        }
    }

    onResumeDataLoaded = (resumeData) => {
        this.setState({resumeData: resumeData});
    };

    render() {
        const {onSectionReached, showGoTop, hideGoTop, resumeData} = this.state;

        if (resumeData) {
            return (
                <div className="App">
                    <Header ref={header => {
                        this.header = header
                    }} resumeData={resumeData}/>
                    <Intro resumeData={resumeData} onSectionReached={onSectionReached} hideGoTop={hideGoTop}/>
                    <About resumeData={resumeData} onSectionReached={onSectionReached} showGoTop={showGoTop}/>
                    <Resume resumeData={resumeData} onSectionReached={onSectionReached}/>
                    {/*<Portfolio resumeData={resumeData} onSectionReached={onSectionReached}/>*/}
                    {/*<Services resumeData={resumeData} onSectionReached={onSectionReached}/>*/}
                    <Stats resumeData={resumeData} onSectionReached={onSectionReached}/>
                    <ContactUs resumeData={resumeData} onSectionReached={onSectionReached}/>
                    <Footer ref={footer => {
                        this.footer = footer
                    }} resumeData={resumeData} onSectionReached={onSectionReached}/>
                </div>
            );
        } else {
            return (
                <div className="App">
                    <Loader onResumeDataLoaded={this.onResumeDataLoaded}/>
                </div>
            );
        }
    }
}

export default App;
