import React, {Component, Fragment} from 'react';
import {Link} from "react-scroll";
import ReactFitText from "react-fittext";
import Waypoint from 'react-waypoint';

export default class Intro extends Component {

    onEnter = () => {
        this.props.onSectionReached('intro');
        this.props.hideGoTop();
    };

    render() {
        const {name, roles, socialLinks} = this.props.resumeData;

        return (
            <Fragment>
                <Waypoint onEnter={this.onEnter} bottomOffset="50%">
                    <div>
                        <section id="intro">
                            <div className="intro-overlay"/>
                            <div className="intro-content">
                                <div className="row">
                                    <div className="col-twelve">
                                        <h5>Hello world!</h5>
                                        <ReactFitText compressor={1} minFontSize={42} maxFontSize={84}>
                                            <h1>I'm {name}</h1>
                                        </ReactFitText>
                                        <p className="intro-position">
                                            {
                                                roles.map(item => {
                                                    return <span key={item}>{item}</span>
                                                })
                                            }
                                        </p>
                                        <Link
                                            className="button stroke smoothscroll"
                                            href="#"
                                            to="about"
                                            spy={true}
                                            smooth={true}
                                            offset={0}
                                            duration={500}
                                        >
                                            More About Me <i className="more-about-me-caret-down fa fa-angle-down"/>
                                        </Link>
                                    </div>
                                </div>
                            </div>
                            {/* /intro-content */}
                            <ul className="intro-social">
                                {
                                    socialLinks.map(item => (
                                        <li key={item.link}><a href={item.link}><i className={item.icon}/></a></li>
                                    ))
                                }
                            </ul>
                            {/* /intro-social */}
                        </section>
                        {/* /intro */}
                    </div>
                </Waypoint>
            </Fragment>
        );
    }
}