import React, {Component, Fragment} from 'react';
import Waypoint from 'react-waypoint';
import axios from 'axios';

export default class ContactUs extends Component {

    constructor() {
        super();

        this.state = {
            contactName: '',
            contactEmail: '',
            contactSubject: '',
            contactMessage: ''
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange = e => {
        this.setState({[e.target.name]: e.target.value});
    };

    handleSubmit = e => {
        e.preventDefault();
        const {contactName, contactEmail, contactSubject, contactMessage} = this.state;

        axios.post('https://anelfdz-portfolio.herokuapp.com/api/sendemail', {
            "contactName": contactName,
            "contactEmail": contactEmail,
            "contactSubject": contactSubject,
            "contactMessage": contactMessage
        })
            .then(response => {
                console.log(response)
            })
            .catch(error => {
                console.error(error);
            });
    };

    render() {
        let data = this.props.resumeData;

        return (
            <Fragment>
                {/*generated code*/}
                <Waypoint onEnter={() => this.props.onSectionReached('contact')} bottomOffset="50%">
                    <div>
                        <section id="contact">
                            <div className="row section-intro">
                                <div className="col-twelve">
                                    <h5>Contact</h5>
                                    <h1>I'd love to hear from you</h1>
                                    <p className="lead">{data.contactDesc}</p>
                                </div>
                            </div>
                            {/* /section-intro */}
                            <div className="row contact-form">
                                <div className="col-twelve">
                                    {/* form */}
                                    <form name="contactForm" id="contactForm" method="post"
                                          onSubmit={this.handleSubmit}>
                                        <fieldset>
                                            <div className="form-field">
                                                <input name="contactName" type="text" id="contactName"
                                                       placeholder="Name" minLength={2} required
                                                       onChange={this.handleChange}/>
                                            </div>
                                            <div className="form-field">
                                                <input name="contactEmail" type="email" id="contactEmail"
                                                       placeholder="Email" required onChange={this.handleChange}/>
                                            </div>
                                            <div className="form-field">
                                                <input name="contactSubject" type="text" id="contactSubject"
                                                       placeholder="Subject" onChange={this.handleChange}/>
                                            </div>
                                            <div className="form-field">
                                                <textarea name="contactMessage" id="contactMessage"
                                                          placeholder="Message" rows={10} cols={50} required
                                                          defaultValue={""} onChange={this.handleChange}/>
                                            </div>
                                            <div className="form-field">
                                                <button className="submitform">Submit</button>
                                                <div id="submit-loader">
                                                    <div className="text-loader">Sending...</div>
                                                    <div className="s-loader">
                                                        <div className="bounce1"/>
                                                        <div className="bounce2"/>
                                                        <div className="bounce3"/>
                                                    </div>
                                                </div>
                                            </div>
                                        </fieldset>
                                    </form>
                                    {/* Form End */}
                                    {/* contact-warning */}
                                    <div id="message-warning">
                                    </div>
                                    {/* contact-success */}
                                    <div id="message-success">
                                        <i className="fa fa-check"/>Your message was sent, thank you!<br/>
                                    </div>
                                </div>
                                {/* /col-twelve */}
                            </div>
                            {/* /contact-form */}
                            <div className="row contact-info">
                                <div className="col-four tab-full">
                                    <div className="icon">
                                        <i className="icon-pin"/>
                                    </div>
                                    <h5>Where to find me</h5>
                                    <p>
                                        {data.address.city}, {data.address.country}
                                    </p>
                                </div>
                                <div className="col-four tab-full collapse">
                                    <div className="icon">
                                        <i className="icon-mail"/>
                                    </div>
                                    <h5>Email Me At</h5>
                                    <p>{data.email}</p>
                                </div>
                                <div className="col-four tab-full">
                                    <div className="icon">
                                        <i className="icon-phone"/>
                                    </div>
                                    <h5>Call Me At</h5>
                                    <p>{data.mobile}</p>
                                </div>
                            </div>
                            {/* /contact-info */}
                        </section>
                        {/* /contact */}
                    </div>
                </Waypoint>
            </Fragment>
        );
    }
}