import React, {Component, Fragment} from 'react';
import Waypoint from 'react-waypoint';
import CountUp from 'react-countup';

export default class Stats extends Component {

    onEnter = () => {
        const {stats} = this.props.resumeData;

        stats.forEach(e => {
            this.refs[e.title].restart();
        });
    };

    easeOutBack = (t, b, c, d) => {
        if ((t /= d / 2) < 1) return c / 2 * t * t * t + b;
        return c / 2 * ((t -= 2) * t * t + 2) + b;
    };

    render() {
        const {stats} = this.props.resumeData;

        return (
            <Fragment>
                <Waypoint onEnter={this.onEnter} bottomOffset="20%">
                    <div>
                        <section id="stats" className="count-up">
                            <div className="row">
                                <div className="col-twelve">
                                    <div className="block-1-6 block-s-1-3 block-tab-1-2 block-mob-full stats-list">
                                        {
                                            stats.map(item => {
                                                return (
                                                    <div className="bgrid stat" key={item.title} id={item.title}>
                                                        <div className="icon-part">
                                                            <i className={item.icon}/>
                                                        </div>
                                                        <h3>
                                                            <CountUp
                                                                ref={item.title}
                                                                end={item.count}
                                                                easingFn={this.easeOutBack}
                                                                className="stat-count"
                                                                duration={2.7}>
                                                            </CountUp>
                                                        </h3>
                                                        <h5 className="stat-title">{item.title}</h5>
                                                    </div>
                                                )
                                            })
                                        }
                                    </div>
                                    {/* /stats-list */}
                                </div>
                                {/* /twelve */}
                            </div>
                            {/* /row */}
                        </section>
                        {/* /stats */}
                    </div>
                </Waypoint>
            </Fragment>
        );
    }
}